import { Card, Col, Container, Row, Table } from "react-bootstrap";
import { baseStyle } from "./BaseStyle";
import ProblemElement from "./ProblemElement";
import { useRef, useState } from "react";
import { Line } from "react-chartjs-2";
import 'chart.js/auto';
import WhiteCard from "./WhiteCard";
import ProblemTable from "./ProblemTable";

export default function Home() {
    return (
        <>
        <Col style={baseStyle} className="text-basic-color">
            <h1 className="m-3">Home</h1>
            <Row className="m-4">
                <Col md={6}>
                    <DailyProblems />
                </Col>
                <Col md={6}>
                    <DailyStatus />
                </Col>
            </Row>
        </Col>
        </>
    );
}

function DailyStatus() {
    return (
        <WhiteCard icon="trophy-fill" title="  ...">
            <p>   : 0</p>
            <p>  : 0</p>
            <DailyStatusChart />
        </WhiteCard>
    );
}

function DailyProblems() {

    const [problems, setProblems] = useState([
        {id: 1000, name: "A+B", solved: 100, level: 1},
        {id: 2839, name: " ", solved: 50, level: 6},
        {id: 7576, name: "", solved: 30, level: 11},
        {id: 6549, name: "   ", solved: 20, level: 16},
        {id: 18185, name: "  (Small)", solved: 10, level: 21},
    ]);

    const headers = ["", " ", "  "];

    return (
        <WhiteCard icon="calendar-check-fill" title=" ">
            <ProblemTable problems={problems} />
        </WhiteCard>
    )
}

function DailyStatusChart() {
    const ref = useRef();

    const data = {
        labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
        datasets: [
          {
            label: '  ',
            data: [100, 150, 300, 430, 450, 600, 700, 800, 1000, 1050, 1100, 1400],
            fill: true,
            backgroundColor: 'rgba(75,192,192,0.2)',
            borderColor: 'rgba(75,192,192,1)'
          },
        ],
    };
    
    return (
        <Line ref={ref} data={data} options={{responsive: true}} />
    )
}