import { useState } from "react";
import { Col, Container, Form, Pagination, Row } from "react-bootstrap";
import { baseStyle } from "./BaseStyle";
import WhiteCard from "./WhiteCard";
import ProblemTable from "./ProblemTable";

export default function Problems() {
    return (
        <>
        <Col style={baseStyle} className="text-basic-color">
            <h1 className="m-3">Unsolved Problems</h1>
            <Row className="m-4">
                <Col md={3}>
                    <Filters />
                </Col>
                <Col md={9}>
                    <ProblemList />
                </Col>
            </Row>
        </Col>
        </>
    );
}

function Filters() {
    const [difficultyLow, setDifficultyLow] = useState(1);
    const [difficultyHigh, setDifficultyHigh] = useState(30);

    const filters = [
        {id: 1, name: ""},
        {id: 2, name: "    ", selected: true},
        {id: 3, name: "      "},
        {id: 4, name: "    "},
        {id: 5, name: "   "},
    ];

    const displayDiff = () => {
        const getImg = (diff) => {
            const tierStyle = {
                width: "20px",
                height: "20px",
                marginRight: "5px",
            }
            const url = `https://static.solved.ac/tier_small/${diff}.svg`;
            return (
                <img src={url} alt="tier" style={tierStyle}></img>
            );
        }
        return (
            <>
            <span className="ms-3">{getImg(difficultyLow)}</span>
            <span className="ms-3">~</span>
            <span className="ms-3">{getImg(difficultyHigh)}</span>
            </>
        )
    };

    const handleDiff = (e, isLow) => {
        const diff = parseInt(e.target.value);
        console.log(isLow, diff, difficultyLow, difficultyHigh);
        if (isLow) {
            setDifficultyLow(diff);
            if (diff > difficultyHigh) {
                setDifficultyHigh(diff);
            }
        } else {
            setDifficultyHigh(diff);
            if (diff < difficultyLow) {
                setDifficultyLow(diff);
            }
        }
    };

    return (
        <WhiteCard icon="filter-square-fill" title=" ">
            <Form className="text-start">
                <Form.Group className="mb-3" controlId="formSearch">
                    <Form.Control type="text" placeholder="" />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formFilter">
                    <Form.Select aria-label=" " className="text-basic-color">
                        {filters.map((filter) => {
                            return <option value={filter.id} selected={filter.selected}>{filter.name}</option>
                        })}
                    </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formDifficulty">
                    <Form.Label>: {displayDiff()}</Form.Label>
                    <Form.Range min="0" max="30" step="1" value={difficultyLow} onChange={(e) => {handleDiff(e, true)}}/>
                    <Form.Range min="0" max="30" step="1" value={difficultyHigh} onChange={(e) => {handleDiff(e, false)}}/>
                </Form.Group>
            </Form>
        </WhiteCard>
    )
}

function ProblemList() {
    const [problems, setProblems] = useState([
        {id: 32399, name: " ", solved: 359, level: 3},
        {id: 32400, name: "", solved: 191, level: 4},
        {id: 32401, name: "ANA ", solved: 174, level: 4},
        {id: 32402, name: "TPS", solved: 82, level: 9},
        {id: 32403, name: "  ", solved: 140, level: 6},
        {id: 32404, name: " ", solved: 108, level: 11},
        {id: 32405, name: " ", solved: 61, level: 12},
        {id: 32406, name: " ", solved: 57, level: 11},
        {id: 32407, name: " ", solved: 57, level: 12},
        {id: 32408, name: "  2", solved: 38, level: 12},
        {id: 32409, name: "", solved: 29, level: 16},
        {id: 32410, name: "  ", solved: 26, level: 15},
        {id: 32411, name: "  ", solved: 20, level: 20},
        {id: 32412, name: "  1", solved: 19, level: 17},
        {id: 32413, name: "  2", solved: 13, level: 22}
    ]);
    const [page, setPage] = useState(1);
    let pages = [];
    for (let i = 1; i <= 5; i++) {
        pages.push(
            <Pagination.Item key={i} active={i === page} onClick={() => {setPage(i)}}
                className="text-basic-color"
            >{i}</Pagination.Item>,
        );
    }

    return (
        <WhiteCard icon="binoculars-fill" title=" ">
            <ProblemTable problems={problems} />
            <Container className="d-flex justify-content-center">
                <Pagination>{pages}</Pagination>
            </Container>
        </WhiteCard>
    )
}